'use server';

import Link from "next/link";

function Workflow({ workflow }) {
    return (
        <Link
            href={"/workflow/" + workflow.id}
        >
            <div className="flow flow-col gap-2 border border-solid border-black rounded-md p-2">
                <h2>{workflow.base_action.metadata[1].key + ": " + workflow.base_action.metadata[1].value}</h2>
                <p>{workflow.base_action.metadata[0].key + ": " + workflow.base_action.metadata[0].value}</p>
            </div>
        </Link>
    )
}

export default async function WorkflowList() {
    const data = await fetch('http://localhost:3001/workflows', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    });
    const workflows = await data.json();

    return (
        <div className="flex flex-col gap-2 border border-solid border-black rounded-xl p-2 w-4/5 m-auto">
            {workflows.map((workflow) => (
                <Workflow key={workflow.id} workflow={workflow} />
            ))}
        </div>
    )
}